import React, { useState } from "react";
import GoogleImage from "./images/unnamed.jpeg"
import ggif from "./images/ggif.gif"
import { backendHost, frontendHost } from "./route";
import axios from "axios"

console.log(backendHost);

const Login = ()=>{

    let [image, setImage] = useState(GoogleImage)

    function play(){
        setImage(ggif)
    }

    function stop(){
        setImage(GoogleImage)
    }

    return(
        <div className="loginDiv">
            <span className="grid head-span">
                <h1 className="login-head">CyberChat</h1>
                <p> login </p>
            </span>
            

            <div className="login-inner">
                <p>Login with your google account click below</p>
                <a className="login-redirect" href={backendHost+"/auth/google"}>
                <img 
                    src={image} 
                    onMouseEnter={play} 
                    onMouseLeave={stop}
                ></img></a>
            </div>
        </div>
    )
}

export default Login;