import React, { useState } from "react";
import GoogleImage from "./images/unnamed.jpeg"
import ggif from "./images/ggif.gif"
import { backendHost, frontendHost } from "./route";
import axios from "axios"


require("dotenv").config()

const SetName = ()=>{

    let [image, setImage] = useState(GoogleImage)

    let [username, setUser] = useState()

    function play(){
        setImage(ggif)
    }

    function stop(){
        setImage(GoogleImage)
    }

    function getUser(event){

        let {value} = event.target
        setUser(value)
    }

    function signUpFunction(params) {
        axios.post(`${backendHost}/setUsername`, {userName: username})
    }

    return(
        <div className="loginDiv">
            <span className="grid head-span">
                <h1 className="login-head">CyberChat</h1>
                <p> Setup Username </p>
            </span>
            

            <form className="login-inner">
                <p>make a small and impactful username :)</p>
                <input 
                    type="text" 
                    className="search" 
                    placeholder="just type your username here"
                    required="true"
                    onChange={getUser}
                    value={username}
                    >
                </input>
                <a type="submit" className="login-redirect-button" onClick={signUpFunction} href={`${frontendHost}/login`}>
                    Next
                </a>
            </form>
        </div>
    )
}

export default SetName;