import React, { useEffect, useRef, useState } from "react";
import ReactDOM  from "react-dom";
import Incoming from "./Incoming";
import Outgoing from "./Outgoing";
import axios from "axios";
import { backendHost, frontendHost } from "../route";
import hideSideBar from "./HideSideBar";

// socket io variables

const ChatArea = (props)=>{

    let [chatValue, setChatValue] = useState()
    let [chatsArray, setChatsArray] = useState([])
    let [friend, setFriend] = useState()
    
    let socket = props.socket
    const dummy = useRef()

    function bottomScroll(params) {
        dummy.current.scrollIntoView({behavior : "smooth"})
    }

    function sendMessage(params) {
        let message = chatValue
        let addMessage = {
            message: message,
            type: "outgoing"
        }

        setChatsArray( prevItems => {
            return( [ 
                ...prevItems, 
                addMessage
            ])
        })


        socket.emit("send-msg", message, props.displayName, props.friend)
        setChatValue("")
    }

    
    useEffect(()=>{

        if (props.friend !== "") {
            axios.post(`${backendHost}/getMessage`, { requestFrom: props.displayName, chatsNeeded: props.friend })
            .then(res => {
                setChatsArray(res.data);
            }).catch(err => console.log(err))

            console.log(chatsArray);
        }

    },[props.friend])

    useEffect(()=>{
        bottomScroll()
    }, [chatsArray])


    useEffect(()=>{

        if (props.message !== "") {
            setChatsArray( prevItems => {
                return ([
                    ...prevItems,
                    props.message
                ])
            })
        }

    }, [props.message])


    let styleHidden = {
        visibility : "hidden"
    }

    let styleShow = {
        visibility : "visible"
    }

    let [showControlIndex, setShowControlIndex] = useState("")  
    let [showFlag, setShowFlag] = useState(false)

    function showControls(index) { 
        setShowControlIndex(index)
    }

    function deleteMessage(index) {
        
        // deleting from other user and database
        let message = chatsArray[index]
        console.log(message);
        socket.emit("deleteMessage", message, props.friend, props.displayName)

        axios.post(`${backendHost}/deleteMessage`, {from: props.displayName, friend: props.friend, message: message})
        .then( res => console.log(res.data))
        .catch( err => console.log(err))

        // deleting from live view
        chatsArray.map((elem, index)=>{
            if (elem === message) {
                console.log("delete message matched on deleter side");
                chatsArray.splice(index,1)
                setChatsArray((prevItems)=>{
                    return([...prevItems])
                })
            }
        })

    }

    useEffect(()=>{
        socket.on("deleteMessage", (message)=>{

            console.log("delete message live triggered");
            
            setChatsArray(chatsArray.filter((elem)=>{
                return elem.message !== message.message
            }))
    
        })
    }, [chatsArray])

    

    function showSidebar(params) {
        console.log("clicked");
        let chatArea = document.querySelector(".chatArea")
        
        hideSideBar()
        chatArea.classList.toggle("hide")
    }

    
    return(
        
        <section className="chatArea">
            <div className="userNamePanel">
            <button className="controlSideBar" onClick={showSidebar}>
                    <ion-icon name="menu"></ion-icon>
                </button>
                {props.friend !== "" ? <p>{props.friend}</p> : <p>select something from menu noone is selected</p>}

            </div>

            
            <div className="chats"> 
            {
                chatsArray.map( (elem, index)=>{        
                    if(elem!== ""){
                        if(elem.type === "outgoing"){
                            return (
                                <div className="messageOutgoingControls">
                                    
                                    {/* showing outgoing messages */}
                                    <Outgoing msg={elem.message} id={index} key={index}/>

                                    {/* showing update and delete buttons */}
                                    <button 
                                        className="showMessageControls" 
                                        onClick={() => {
                                            setShowFlag( showFlag => !showFlag)
                                            showControls(index)
                                        }} 
                                        > 
                                        <ion-icon name="ellipsis-vertical-circle"></ion-icon> 
                                    </button>

                                    <div 
                                        className="messageControls" 
                                        style={showControlIndex === index && showFlag === true? styleShow : styleHidden}>
                                        <button 
                                            className="messageControlButton" 
                                            value={index} 
                                            onClick={() => deleteMessage(index)}> delete </button>
                                        <button 
                                            className="messageControlButton" 
                                            value={index}> edit </button>
                                    </div> 
                                    
                                </div>
                            )
                        }else if(elem.type === "incoming"){
                            return <Incoming msg={elem.message} id={index} key={index}/>
                        }   
                    }   
                })

            }

            <div ref={dummy}></div>

            </div>


            <div className="flex sendArea">
                
                <textarea 
                className="type" 
                placeholder="Type your chat here " 
                onChange={(event)=>{
                    let {value} = event.target
                    setChatValue(value)
                }}
                value={chatValue}></textarea>
                
                <button onClick={sendMessage} className="send"><ion-icon name="send"></ion-icon></button>
            </div>

            
                   


        </section>
    )
    
}


// let changeDiv = document.querySelector(".chats");
// changeDiv.scrollTop = changeDiv.scrollHeight;   


export default ChatArea;