import React from "react";
import sideImage from "./images/sideImage.png"
import icon from "./images/icon.png"
import arrow from "./images/arrow.png"
import { backendHost } from "./route";

const Home = ()=>{
    console.log(backendHost);
    return(
        <section className="home">
            <div className="navBar">
                <div className="branding-div flex">
                    <img className="head-icon" src={icon}></img>
                    <span className="grid">
                        <h1 className="branding">CyberChat</h1>
                        <p className="brandName">ComputaVerse</p>
                    </span>
                </div>

                <ul className="navbar-options">
                    <a className="navOption" href="#">terms</a>
                    <a className="navOption" href="/login">login</a>
                    <a className="navOption" href="/signup">signup</a>
                </ul>
            </div>
            <div className="home-main">
                <div className="home-main-content">
                    <span>
                        <span className="flex"> 
                            <h2 style={{fontWeight: "500"}}>The most</h2> 
                            <h1 style={{color: "#AD43F5", fontSize: "220%"}}>Reliable, </h1> 
                        </span>
                        <span className="flex"> 
                            <h1 style={{color: "#EE58AE", fontSize: "220%"}}>Responsive, </h1> 
                            <h1 style={{color: "#FC5A54", fontSize: "220%"}}>Safe, </h1> </span>
                        <h1 style={{fontSize: "220%", fontStyle: "italic"}}>Chat App</h1>      
                    </span>

                    <p>just use your google account and hop on in the world of chatting</p>

                    <div>
                        <span className="flex">
                            <a className="signUpButton" href="/login">signUp</a>
                            <a className="loginButton" href="/login">logIn</a>
                        </span>
                        <br />
                        <br />
                        <p className="brandName">© Samannaway 2023</p>

                    </div>

                </div>
                <img className="home-main-image" src={sideImage}/>
            </div>
        </section>
    )
}


export default Home;