import React, { useEffect, useState } from "react";
import ChatNav from "./ChatNav";
import ChatArea from "./ChatArea";
import axios from "axios";
import {backendHost, frontendHost} from "../route";


const Chat = (props)=>{


    let [friendSelected, setFriendSelected] = useState("");
    let socket = props.socket
    let [message, setMessage] = useState("")


    function setPerson(personPassed) {

        // axios.post(`${backendHost}/setPerson`, {person: personPassed}) // emit this person to the socket and then send only if they match
        // .then(res => console.log(res.data))
        // .catch(err => console.log(err))
        
        setFriendSelected(personPassed)
        
        let chatNav = document.querySelector(".chatNav")
        let chatArea = document.querySelector(".chatArea")

        chatNav.classList.toggle("chatNavMobile")
        chatArea.classList.toggle("hide")

        // socket.emit("set-friend", socket, personPassed)
    }

    useEffect( ()=>{
        socket.on("recieve-msg", (message, from, to)=>{


            console.log(from, friendSelected);
            if (from == friendSelected) {

                let messageToBeAdded = {
                    message : message,
                    type : "incoming"
                }
    
                setMessage(messageToBeAdded)
            
            }
        })

        return() => {
            socket.off("recieve-msg")
        }

    }, [friendSelected])

    return(
        <div className="flex">
            <ChatNav displayName= {props.displayName} setPerson={setPerson}/>
            <ChatArea displayName= {props.displayName} friend={friendSelected} socket={props.socket} io={props.io} message={message}/>
        </div>
    )
}

export default Chat;