import React, { useEffect, useState } from "react";
import Person from "./Person";
import axios from "axios";
import { backendHost } from "../route";
import hideSideBar from "./HideSideBar";

const chatNav = (props)=>{

    let [FriendsArray, setFriendsArray] = useState([])
    let chatNav = document.querySelector(".chatNav")

    function getFriends() {
        console.log("executed");
        axios.get(`${backendHost}/friends`,)
        .then(response => setFriendsArray(response.data.foundFriends))
        .catch(err => console.log(err))
    }

    useEffect(()=>{getFriends()}, [])

    return(
        <section className="chatNav chatNavMobile">
            <input className="search" placeholder="search"></input>
            <button className="controlSideBar" onClick={()=>{
                hideSideBar()
                chatNav.classList.toggle("chatNavMobile")
            }
                
            }>
                <ion-icon name="menu"></ion-icon>
            </button>
            <p className="head1">chats</p>
            <div className="people">
                {
                    FriendsArray.map((elem,index)=>{
                        return(<Person person={elem} key={index} setPerson={props.setPerson}/>)
                    })
                }

            </div>

        </section>
    )
}

export default chatNav;