import React, { useEffect, useState } from "react";
import axios from "axios";
import Person from "./Person";
import hideSideBar from "./HideSideBar";
import tryToggle from "./toggleHide";
import { backendHost } from "../route";


const Requests = (props)=>{

    let [requests, setRequests] = useState([])

    function getRequests(params) {
        axios.get(`${backendHost}/requests`, {username: props.displayName})
        .then(res => {
            setRequests(res.data)
            console.log(res);
        })
        .catch(err=> console.log(err))
    }

    useEffect(()=>{
        getRequests()
    },[])

    const liveDelete = (index)=>{
        setRequests(requests.filter((elem, indexForFilter)=>{
            return indexForFilter!==index
        }))
    }

    const accept = (index)=>{
        let user = requests[index].name
        axios.post(`${backendHost}/add`, {username: props.displayName, friend: user})
        .then( res=> {
            console.log(res)
            getRequests()
        })
        .catch(err => console.log(err))


        liveDelete(index)

    }

    const reject = (index)=>{
        
        let user = requests[index].name
        axios.post(`${backendHost}/deleteRequest`, {friend: user, username: props.displayName})
        .then(res=> console.log(res.data))
        .catch(err=> console.log(err))

        liveDelete(index)

    }

    function dummy(params) {
        console.log("nothing on clicking that :)");
    }

    
    return(
        <div className="requests">
            <div className="flex">

                <button className="controlSideBar" onClick={()=>{
                        hideSideBar()
                        tryToggle("requests")
                    }}><ion-icon name="menu"></ion-icon>
                </button>

                <h1 className="friends-heading"> Friend Requests</h1>

            </div>
            {
                requests.map( (elem, index)=>{
                    return  (
                        <div className="request-elem">
                            <Person person={elem.name} setPerson={dummy}/>
                            <button 
                                className="accept-btn" 
                                onClick={()=>accept(index)}
                            >Accept</button>
                            <button 
                                className="reject-btn" 
                                onClick={()=>reject(index)}
                            >reject</button>    
                        </div>
                    )
                })
            }
        </div>
    )
}

export default Requests;