import React, { useEffect, useState } from "react";
import Person from "./Person"
import ReactDOM  from "react-dom";
import axios, { Axios } from "axios"
import { backendHost } from "../route";
import hideSideBar from "./HideSideBar";

const Friends = (props)=>{


    let [friendsArray, setFriendsArray] = useState([])
    let [findFriendInput, setFindFriendInput] = useState()

    function getFriends() {
        console.log("executed");
        axios.get(`${backendHost}/friends`,)
        .then(response => setFriendsArray(response.data.foundFriends))
        .catch(err => console.log(err))
    }

    function findFriend() {
        
        friendsArray.map((elem)=>{
            if (elem === findFriendInput) {
                setFriendsArray([`${elem}`])
            }else{
                return;
            }
        })

        setFindFriendInput("")
    }

    function dummy(params) {
        console.log("nothing on clicking that :)");
    }

    function deleteFriend(friend, user) {
        axios.post(`${backendHost}/deleteFriend`, {friend: friend, user: user})
        .then(
            res => {
                getFriends()
                console.log(res.data.status) 
            })
        .catch(err => console.log(err))

        
    
    }

    useEffect(()=>{
        getFriends()
    }, [])

    
    return (
        <section className="friends">
            <div className="flex">
                <button className="controlSideBar" onClick={()=>{
                    hideSideBar()
                    document.querySelector(".friends").classList.toggle("hide")
                }}>
                    <ion-icon name="menu"></ion-icon>
                </button>
                <h1 className="friends-heading">Friends</h1>
            </div>
            <input 
                class="search add-input search-friend" 
                placeholder="search friends here" 
                onChange={(event)=>{
                    let {value} = event.target
                    setFindFriendInput(value)
                }}
                value={findFriendInput}></input>
            
            <div className="friends-btn-div">
                <span className="friends-btn-area">
                    <button className="send search-btn friends-btn" onClick={getFriends}>Clear Search</button>
                    <button className="send search-btn friends-btn" onClick={findFriend}>Search</button>
                </span>
            </div>

            {
                friendsArray.map((elem,index)=>{
                    return (
                        <div className="friendElem">
                            <Person key={index} person={elem} setPerson={dummy}/>
                            <button className="remove" onClick={()=>deleteFriend(elem,props.displayName)}> 
                                <ion-icon name="trash-bin-outline"></ion-icon> 
                                <h1>remove friend</h1>
                            </button>
                        </div>
                    )
                })
            }

            
            
        </section>
    )
}

export default Friends;