import React, { useEffect, useState } from "react";
import Main from "./Main";

import { io } from "socket.io-client";
import { backendHost, frontendHost } from "../route";
const socket = io(frontendHost)

// window.addEventListener("beforeunload", ()=>{
//     socket.disconnect()
//     socket.emit("disconnect")
// })

let MainApp = (props)=>{


    useEffect(()=>{
        socket.emit("login", props.displayName)
    }, [])

    
    


    return(
        <div className="mainDiv">
            <Main displayName= {props.displayName} io={io} socket={socket}/>
        </div>
    )
}

export default MainApp;