import React, { useEffect, useState } from "react";
import Chat from "./Chat";
import Friends from "./Friends"
import Add from "./Add"
import axios from "axios";
import Requests from "./Requests";
import hideSideBar from "./HideSideBar";
import tryToggle from "./toggleHide";
import { backendHost } from "../route";

const Main = (props)=>{

    let [RenderElem, setRenderElem] = useState("chat")

    const logout = ()=>{
        axios.post(`${backendHost}/verification`,{status : "unverified"})
    }

    function showSidebar(params) {
        hideSideBar()
        tryToggle("friends")
        tryToggle("chatArea")
        tryToggle("requests")
        tryToggle("add")
    }

    function toggleChatNav(params) {
        let chatNav = document.querySelector(".chatNav")
        hideSideBar()
        try{        
            chatNav.classList.toggle("chatNavMobile")
        }catch(err){
            console.log(err)
        }
    }

    return(
        <section className="mainDiv"> 

            <section className="sideBar">
                <div className="sidebar-header">
                    <button className="controlSideBar" onClick={()=>{showSidebar()}}>
                        <ion-icon name="menu"></ion-icon>
                    </button>
                    <h1 className="heading"> CyberChat</h1>
                    <span className="flex">
                        <ion-icon name="person-circle"></ion-icon> <p className="account">{props.displayName}</p>
                    </span>
                    <a className="flex logout" onClick={logout} href={backendHost+"/logout"}>
                        <ion-icon name="log-out-outline"></ion-icon> Logout
                    </a>
                </div>
                

                <div className="sidebar-options">

                    <button className="s-option" onClick={()=>{
                        setRenderElem("chat")
                        toggleChatNav()
                    }} >
                        <i className="fa-solid fa-message"></i>
                    </button>
                    <button className="s-option" onClick={()=>{
                        setRenderElem("friends")
                        hideSideBar()
                        tryToggle("hide")
                    }} >
                        <ion-icon name="people-circle"></ion-icon>
                    </button>
                    <button className="s-option" onClick={()=>{
                        setRenderElem("requests")
                        hideSideBar()
                        tryToggle("requests")
                    }}>
                        <ion-icon name="notifications-circle-outline"></ion-icon>
                    </button>
                    <button className="s-option" onClick={()=>{
                        setRenderElem("add")
                        hideSideBar()
                        tryToggle("add")
                    }} >
                        <ion-icon name="person-add"></ion-icon>
                    </button>
                </div>    
            </section>

            
            {RenderElem === "chat"&& <Chat displayName= {props.displayName} socket={props.socket} io={props.io}/>}
            {RenderElem === "friends"&& <Friends displayName= {props.displayName}/>}
            {RenderElem === "add"&& <Add displayName= {props.displayName}/>}
            {RenderElem === "requests" && <Requests displayName={props.displayName} socket={props.socket} io={props.io}/>}
        </section>
    )
}

export default Main