import React, { useState } from "react";
import ReactDOM  from "react-dom";
import axios from "axios"
import { backendHost } from "../route";
import tryToggle from "./toggleHide";
import hideSideBar from "./HideSideBar";

const Add = (props)=>{

    let [add,setAdd]= useState()

    function changeValue(event) {
        let {value} = event.target
        setAdd(value)
    }

    function addFriend() {
        axios.post(`${backendHost}/requests`, {username: props.displayName, request: add})
        .then(res=>console.log(res.data))
        setAdd("")
    }

    return (
        <section className="add">
            <div className="flex">
                
                <button className="controlSideBar" onClick={()=>{
                        hideSideBar()
                        tryToggle("add")
                    }}><ion-icon name="menu"></ion-icon>
                </button>
                
                <h1 className="friends-heading">Add Friends</h1>
            </div>
            <input 
                className="search add-input" 
                placeholder="enter username here" 
                value={add}
                onChange={changeValue}
            ></input>
            <button className="send search-btn" onClick={addFriend}>Add</button>
        </section>
    )
}

export default Add;